import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
    width: 100%;
    height: 100%;
    padding: 10px;
`;

const Header = styled.div`
    font-weight: bold;
    font-size: 17px;
    box-shadow: 0px 2px 3px rgb(0 0 0 / 7%);
    padding: 10px;
    margin-bottom: 10px;
`;

export default function Contents({ data }) {
    return (
        <Container>
            <Header>Your Search</Header>
            {data.content}
        </Container>
    );
}
