import React, { useState } from 'react';
import { Input } from '@components/libs/Forms';
import Button from '@components/libs/Button';
import styled from 'styled-components';
import Cookies from 'js-cookie';
import Modal from '@modules/Modal';
import Authe from '../Authe';
import RequestAction from './RequestAction';

import Company from '../components/Company';
import Disclaimer from '../components/Disclaimer';
import Scammed from '../components/Scammed';

const SendButton = styled(Button)`
    width: 60%;
    height: 40px;
`;

export default function SearchEmail() {
    const [state, setState] = useState({
        email: '',
        response: false,
        onRequest: false,
        noInfo: false,
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    const searchemail = async () => {
        if (typeof Cookies.get('SOJI_uft_token') != 'undefined') {
            stateHandler('requestActionModal', true);
        } else {
            stateHandler('autheModal', true);
        }
    };

    return (
        <>
            <Input
                placeholder="Paste your E-mail address here for verification"
                onChange={(e) => stateHandler('email', e.target.value)}
            />
            {state.response &&
                state.response.map((result) => {
                    if (result.isScam == 'Yes') {
                        return <Scammed result={result} />;
                    } else if (result.isScam == 'No') {
                        return <Company result={result} />;
                    } else if (result.isScam == 'Disclaimer') {
                        return <Disclaimer result={result} content={result.searchedContent} contentType="Email" />;
                    } else if (result.isScam == 'No Token') {
                        return <p>{result.description}</p>;
                    } else {
                        return <p>No Information yet</p>;
                    }
                })}
            {state.noInfo && <p style={{ color: 'red', fontWeight: 700 }}>{state.noInfo}</p>}
            <div className="button_container">
                <SendButton title="Search" className="button" loading={state.onRequest} onClick={() => searchemail()} />
            </div>
            <Modal visible={state.autheModal} onRequestClose={() => stateHandler('autheModal', false)}>
                <Authe onRequestClose={() => stateHandler('autheModal', false)} stateHandler={stateHandler} />
            </Modal>
            <Modal visible={state.requestActionModal} onRequestClose={() => stateHandler('requestActionModal', false)}>
                <RequestAction
                    onRequestClose={() => stateHandler('requestActionModal', false)}
                    state={state}
                    stateHandler={stateHandler}
                />
            </Modal>
        </>
    );
}
