import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import PagePreloader from '@components/preloader/PagePreloader';
import Modal from '@modules/Modal';
import SojiBaseInit from '@modules/SojiBase';

import SearchResult from '../SearchResult';

const Container = styled.div`
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
`;

export default function RequestAction({ userTextToScan, onRequestClose }) {
    const [state, setState] = useState({
        response: false,
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    useEffect(() => {
        (async () => {
            const payload = {
                text: userTextToScan,
            };
            const SojiBase = await SojiBaseInit;
            const getter = await SojiBase.post('v1/search_engine/search_texts', payload);
            getter.on('static', (response) => {
                if (response.statusCode == 200) {
                    const data = response.response.data;
                    stateHandler('response', data);
                    stateHandler('openResultModal', true);
                }
            });
        })();
    }, []);

    const closeModal = () => {
        onRequestClose();
    };

    return (
        <>
            {state.response ? (
                <SearchResult data={state.response} onRequestClose={() => closeModal()} />
            ) : (
                <PagePreloader />
            )}
        </>
    );
}
