import React, { useState, useEffect } from 'react';
import SearchPhoneNumber from './SearchPhoneNumber';
import SearchEmail from './SearchEmail';
import SearchText from './SearchText';
import SearchCompanies from './SearchCompanies';
import mixpanel from '@components/mixpanel';
import styled from 'styled-components';
import SEO from '@modules/SEO';
import { generateHeadTags } from 'react-seo-tools/lib/generateHeadTags';
import { Helmet } from 'react-helmet';
import './index.css';

const ButtonContainer = styled.div`
    width: 100%;
`;

export default function ScamSearch() {
    const [page, setPage] = useState('company');

    useEffect(() => {
        //mixpanel
        mixpanel.track('Sojiare User Search', {
            'Opted out of email': true,
            'ip': '0',
        });
    }, []);

    return (
        <>
            <>
                <SEO
                    title="Sojiare - Verify what is Original or Suspicious"
                    keywords="sojiare, gavelng, gavel, soji, solitaire, sojiare.com, online dating, scammer, scam, scam bible, scam likely, online safety, cyber security, how to scam, scam calls, scam likely, scam bible, business verification, online safety, exposing scams, scam episode, cash app scam, facebook marketplace scam, scam on facebook marketplace, scam likely, social security scam, publishers clearing house scam, scam publishers clearing house, is publisher clearing house a scam scammer payback, scambaiter, scammer gets scammed"
                    description="SojiAre.com protects you from online scam by allowing you to verify companies, contents, numbers and emails. Verify what is Original or Suspicious!"
                />
                <div className="tab_container">
                    <div className={`tab_button ${page == 'company' && 'active'}`} onClick={() => setPage('company')}>
                        Company
                    </div>
                    {/* <div className={`tab_button ${page == 'link' && 'active'}`} onClick={() => setPage('link')}>
                        Link
                    </div> */}
                    <div
                        className={`tab_button ${page == 'phoneNumber' && 'active'}`}
                        onClick={() => setPage('phoneNumber')}
                    >
                        <ButtonContainer>Number</ButtonContainer>
                    </div>
                    <div className={`tab_button ${page == 'email' && 'active'}`} onClick={() => setPage('email')}>
                        Email
                    </div>
                    {/* <div
                        className={`tab_button ${page == 'documents' && 'active'}`}
                        onClick={() => setPage('documents')}
                    >
                        Document's
                    </div> */}
                </div>
                {page == 'documents' && <SearchText />}
                {page == 'phoneNumber' && <SearchPhoneNumber />}
                {page == 'email' && <SearchEmail />}
                {page == 'company' && <SearchCompanies />}
                {/* {page == 'link' && <SearchCompanies />} */}

            </>
        </>
    );
}
