import React, { useState } from 'react';
import Button from '@components/libs/Button';
import { Input } from '@components/libs/Forms';
import Cookies from 'js-cookie';
import Modal from '@modules/Modal';
import Authe from '../Authe';
import RequestAction from './RequestAction';
import styled from 'styled-components';

const SendButton = styled(Button)`
    width: 60%;
    height: 40px;
`;

export default function SearchText() {
    const [state, setState] = useState({
        userTextToScan: '',
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    const searchAction = async () => {
        const text = state.userTextToScan.split(' ');
        if (text.length < 4) {
            stateHandler('error', 'Please make sure your search entry is up to four characters');
        } else {
            if (typeof Cookies.get('SOJI_uft_token') != 'undefined') {
                stateHandler('requestActionModal', true);
            } else {
                stateHandler('autheModal', true);
            }
        }
    };

    return (
        <>
            <div className="verification_form_box">
                <div className="verification_box_content">
                    <Input
                        textarea
                        value={state.userTextToScan}
                        onChange={(e) => stateHandler('userTextToScan', e.target.value)}
                        className="verification_form_input"
                        placeholder="Paste document format here for verification"
                    ></Input>
                    {state.error && <p style={{ color: 'red', fontWeight: 700 }}>{state.error}</p>}
                    <div className="button_container">
                        <SendButton title="Continue" className="button" onClick={() => searchAction()} />
                    </div>
                </div>
            </div>
            <Modal visible={state.autheModal} onRequestClose={() => stateHandler('autheModal', false)}>
                <Authe onRequestClose={() => stateHandler('autheModal', false)} stateHandler={stateHandler} />
            </Modal>
            <Modal visible={state.requestActionModal} onRequestClose={() => stateHandler('requestActionModal', false)}>
                <RequestAction
                    onRequestClose={() => stateHandler('requestActionModal', false)}
                    userTextToScan={state.userTextToScan}
                />
            </Modal>
        </>
    );
}
