import React, { useState } from 'react';
import styled from 'styled-components';
import { Input } from '@components/libs/Forms';
import Button from '@components/libs/Button';
import { colors } from '@components/typo';
import SojiBaseInit from '@modules/SojiBase';
import { useAlertState } from '@components/libs/Notify/useAlertState';
import './index.css';

const Container = styled.div`
    .addToVerifyContainer {
        width: 500px;
        background-color: #fff;
        border-radius: 10px;
        padding: 15px;
    }
    @media only screen and (max-width: 500px) {
        .addToVerifyContainer {
            width: 95%;
        }
    }
`;

const Title = styled.div`
    font-weight: 600;
    margin-top: 10px;
    margin-bottom: 10px;
`;

const ActionButtonContainer = styled.div`
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
`;

const SendButton = styled(Button)`
    width: 150px;
    height: 40px;
    background-color: ${colors.defaultColor};
    color: #fff;
    border-radius: 5px;
`;
const CancelButton = styled(Button)`
    width: 80px;
    height: 40px;
    background-color: ${colors.backgroundColor};
    border-radius: 5px;
`;

const CheckBoxButton = styled.div`
    width: 100%;
    padding: 10px;
    background-color: ${({ active }) => (active ? colors.defaultColor : colors.grey)};
    margin-bottom: 3px;
    color: ${({ active }) => (active ? '#fff' : colors.textColor)};
    cursor: pointer;
`;

const Line = styled.div`
    width: 100%;
    height: 2px;
    background-color: ${colors.backgroundColor};
    margin-top: 5px;
    margin-bottom: 5px;
`;

const AddToFlag = ({ searchedContent, onRequestClose, contentType }) => {
    const alertState = useAlertState();
    const [state, setState] = useState()
    const [loading, setLoading] = useState(false);
    const [reason, setReason] = useState(false);

    const addToVerify = async () => {
        setLoading(true);
        const SojiBase = await SojiBaseInit;
        const payload = { content: searchedContent, reason};

        const getter = await SojiBase.post(
            `v1/search_engine/${contentType == 'Phone Number' ? 'flag_phone_numer' : 'flag_email'}`,
            payload,
        );
        getter.on('static', (response) => {
            setLoading(false);
            onRequestClose();
            alertState.set({
                state: true,
                title: 'Notification',
                desc: response.response.data,
                type: 'success',
            });
        });
    };

    return (
        <Container>
            <div className="addToVerifyContainer">
                <Title>
                    <div>
                        Are you sure you want to flag <b style={{ color: 'red' }}>{searchedContent}</b> as a suspicious{' '}
                        {contentType ? 'Phone Number' : 'Email'}?
                    </div>
                </Title>
                <Line />
                <Title>Report Reasons</Title>
                <div>
                    <CheckBoxButton active={state == 'Scammed'} onClick={() => {
                        setReason('Scammed')
                        setState("Scammed")
                    }}>
                        I was scammed through the number or email
                    </CheckBoxButton>
                    <CheckBoxButton active={state == 'Suspicious SMS'} onClick={() => {
                        setReason('Suspicious SMS')
                        setState("Suspicious SMS")
                    }}>
                        I received a suspicious phone call or SMS through this number{' '}
                    </CheckBoxButton>
                    <CheckBoxButton active={state == 'others'} onClick={() =>{
                         setState("others")
                    }}>
                        Others
                    </CheckBoxButton>
                </div>
                {state == 'others' && (
                    <Input placeholder="Give Reasons" onChange={(e) => setReason(e.target.value)} />
                )}

                <ActionButtonContainer>
                    <CancelButton title="Cancel" onClick={() => onRequestClose()} />
                    <SendButton
                        className="button_container"
                        title="Ok, Add"
                        loading={loading}
                        onClick={() => addToVerify()}
                    />
                </ActionButtonContainer>
            </div>
        </Container>
    );
};

export default AddToFlag;
