import React from 'react';
import './index.css';

export default function Suggestions({ data }) {
    const { suggestions } = data;
    return (
        <div className="suggestions_box">
            <p className="suggestions_box_title">Security Suggestions</p>
            <div className="sugestions_box_wrapper">
                {suggestions.map((data, index) => {
                    return (
                        <div className="suggest" key={index}>
                            <div className="bullet"></div>
                            <div className="suggestion_text">{data}</div>
                        </div>
                    );
                })}
            </div>
        </div>
    );
}
