import React from 'react';
import styled from 'styled-components';

const Title = styled.p`
    font-weight: bold;
    color: green;
`;

const Wrapper = styled.div`
    display: flex;
    width: 100%;
    margin: 10px;
`

const HeaderTitle = styled.div`
    width: 140px;
`
const Desc = styled.div`
    flex: 1;
    margin-left: 10px;
`

export default function Company({result}) {
    return (
        <>
            <Title>{result.description}</Title>
            <Wrapper>
                <HeaderTitle>Company Name</HeaderTitle>
                <Desc>{result.dataset.companyName}</Desc>
            </Wrapper>
            <Wrapper>
                <HeaderTitle>Email</HeaderTitle>
                <Desc>{result.dataset.email}</Desc>
            </Wrapper>
            <Wrapper>
                <HeaderTitle>Phone Number</HeaderTitle>
                <Desc>{result.dataset.phoneNumber}</Desc>
            </Wrapper>
            <Wrapper>
                <HeaderTitle>Website</HeaderTitle>
                <Desc>{result.dataset.website}</Desc>
            </Wrapper>
        </>
    );
}
