import React, { useState } from 'react';
import { Input } from '@components/libs/Forms';
import Button from '@components/libs/Button';
import styled from 'styled-components';
import Cookies from 'js-cookie';
import Modal from '@modules/Modal';
import { useNavigate } from 'react-router-dom';
import Authe from '../Authe';
import {AutoSuggest} from '@components/libs/Forms';
import RequestAction from './RequestAction';

const SendButton = styled(Button)`
    width: 60%;
    height: 40px;
`;

export default function SearchCompanies() {
    const [state, setState] = useState({
        company: '',
        pageHandle: '',
        response: false,
        onRequest: false,
        noInfo: false,
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    const searchCompany = async () => {
        if (typeof Cookies.get('SOJI_uft_token') != 'undefined') {
            stateHandler('requestActionModal', true);
        } else {
            stateHandler('autheModal', true);
        }
    };

    return (
        <>
            <AutoSuggest
                getOptionLabel={(e) => e.companyName}
                getOptionValue={(e) => e.pageHandle}
                onChange={(value) => {
                    stateHandler('company', value);
                    stateHandler('pageHandle', value.pageHandle);
                }}
                value={state.company}
                placeholder="Company Name/Link"
            />
            <div className="button_container">
                <SendButton
                    title="Search"
                    className="button"
                    loading={state.onRequest}
                    onClick={() => searchCompany()}
                />
            </div>
            <Modal visible={state.autheModal} onRequestClose={() => stateHandler('autheModal', false)}>
                <Authe onRequestClose={() => stateHandler('autheModal', false)} stateHandler={stateHandler} />
            </Modal>
            <Modal visible={state.requestActionModal} onRequestClose={() => stateHandler('requestActionModal', false)}>
                <RequestAction
                    onRequestClose={() => stateHandler('requestActionModal', false)}
                    state={state}
                    stateHandler={stateHandler}
                    pageHandle={state.pageHandle}
                />
            </Modal>
        </>
    );
}

