import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import SojiBaseInit from '@modules/SojiBase';
import Preloader from '@components/preloader';

export default function RequestAction({ pageHandle }) {
    const navigate = useNavigate();
    useEffect(() => {
        (async () => {
            const SojiBase = await SojiBaseInit;
            const payload = { search: pageHandle };
            const getter = await SojiBase.post('v1/search_engine/save_searches', payload);
            getter.on('static', async (response) => {
                if (response.response.status) {
                    navigate(`/company/${pageHandle}`);
                }
            });
        })();
    }, []);
    return <Preloader />
}
