import React from 'react';
import styled from 'styled-components';
import {IoCloseSharp} from 'react-icons/io5'
import './index.css';

import Contents from './components/Contents';
import Results from './components/Results';

const Container = styled.div`
    width: 100%;
`;

export default function SearchResult({onRequestClose, data}) {
    if(data.length > 0){
        return (
            <Container>
                <div className="container content_Container">
                    <div className="search_modal_header">
                        <div>Search Results</div>
                        <div className='close_button' onClick={() => onRequestClose()}>
                            <IoCloseSharp size={24} />
                        </div>
                    </div>
                    <div className="row content_wrapper">
                        <div className="col-md-6">
                            <Contents data={data[0]} />
                        </div>
                        <div className="col-md-6">
                            <Results data={data[0]} />
                        </div>
                    </div>
                </div>
            </Container>
        );
    }else {
        return (
            <Container>
                <div className="container content_Container">
                    <div className="search_modal_header">
                        <div>Search Results</div>
                        <div className='close_button' onClick={() => onRequestClose()}>
                            <IoCloseSharp size={24} />
                        </div>
                    </div>
                    <div className="content_wrapper">
                        No Information yet about this Content
                    </div>
                </div>
            </Container>
        );
    }
   
}



