import React, {useEffect} from 'react';
import styled from 'styled-components';
import { useAlertState } from '@components/libs/Notify/useAlertState';


const Title = styled.p`
    font-weight: bold;
    color: green;
    color: red;
`;

export default function Scammed({result}) {
    const alertState = useAlertState();

    useEffect(() => {
        (async () => {
            alertState.set({
                state: true,
                title: 'Notification',
                desc: result.description,
                type: 'success',
            });
        })()
    }, [])

    return (
        <>
            <Title>{result.description}</Title>
        </>
    );
}
