import React, { useState } from 'react';
import styled from 'styled-components';
import { Input } from '@components/libs/Forms';
import Button from '@components/libs/Button';
import { colors } from '@components/typo';
import SojiBaseInit from '@modules/SojiBase';
import { useAlertState } from '@components/libs/Notify/useAlertState';

const Container = styled.div`
    .addToVerifyContainer {
        width: 300px;
        background-color: #fff;
        border-radius: 10px;
        padding: 15px;
    }
`;

const Title = styled.div`
    font-weight: 600;
`;
const VerifyContent = styled.span`
    color: green;
`;

const ActionButtonContainer = styled.div`
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
`;

const SendButton = styled(Button)`
    width: 150px;
    height: 40px;
    background-color: ${colors.defaultColor};
    color: #fff;
    border-radius: 5px;
`;
const CancelButton = styled(Button)`
    width: 80px;
    height: 40px;
    background-color: ${colors.backgroundColor};
    border-radius: 5px;
`;

const AddToVerify = ({ searchedContent, onRequestClose, contentType }) => {
    const alertState = useAlertState();
    const [loading, setLoading] = useState(false);
    const [companyName, setCompanyName] = useState();
    const [companyEmail, setCompanyEmail] = useState();

    const addToVerify = async () => {
        setLoading(true);
        const SojiBase = await SojiBaseInit;
        const payload = { content: searchedContent, companyName, companyEmail };
        const getter = await SojiBase.post(
            `v1/search_engine/${contentType == 'Phone Number' ? 'verify_phone_number' : 'verify_email'}`,
            payload,
        );
        getter.on('static', (response) => {
            setLoading(false);
            onRequestClose();
            alertState.set({
                state: true,
                title: 'Notification',
                desc: response.response.data,
                type: 'success',
            });
        });
    };

    return (
        <Container>
            <div className="addToVerifyContainer">
                <Title>
                    Verify <VerifyContent>{searchedContent}</VerifyContent>
                </Title>
                <Input placeholder="Company Name" onChange={(e) => setCompanyName(e.target.value)} />
                <Input placeholder="Company Email" onChange={(e) => setCompanyEmail(e.target.value)} />
                <ActionButtonContainer>
                    <CancelButton title="Cancel" onClick={() => onRequestClose()} />
                    <SendButton
                        className="button_container"
                        title="Ok, Add"
                        loading={loading}
                        onClick={() => addToVerify()}
                    />
                </ActionButtonContainer>
            </div>
        </Container>
    );
};

export default AddToVerify;
