import React, { useState } from 'react';
import { Input } from '@components/libs/Forms';
import Button from '@components/libs/Button';
import styled from 'styled-components';
import SojiBaseInit from '@modules/SojiBase';
import '../../index.css';

const SendButton = styled(Button)`
    width: 60%;
    height: 40px;
`;

export default function ForgotPassword({ pageHandler }) {
    const [email, setEmail] = useState();
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState();
    const [success, setSuccess] = useState();

    const forgotPassword = async () => {
        setSuccess(false)
        setError(false)
        const SojiBase = await SojiBaseInit;
        const payload = { email };
        setLoading(true);
        const getter = await SojiBase.post('v1/authe/forgot_password', payload);
        getter.on('static', (response) => {
            console.log(response)
            setLoading(false);
            if (response.response) {
                if (response.response.status == 403) {
                    setError(response.response.data);
                } else {
                    setSuccess(response.response.data);
                }
            }
        });
    };

    return (
        <div className="authe_container">
            <div className="title">Forgot Password</div>
            <Input placeholder="Email" onChange={(e) => setEmail(e.target.value)} />
            <div className="authe_action_box">
                <p>
                    Have an account?
                    <a
                        onClick={() => pageHandler('pageName', 'login')}
                        className="text_default_color text_bold cursor_pointer"
                    >
                        Sign In
                    </a>
                </p>
            </div>
            <p style={{ color: 'red' }}>{error && error}</p>
            <p style={{ color: 'green' }}>{success && success}</p>
            <div className="button_container">
                <SendButton className="button" loading={loading} title="Send" onClick={() => forgotPassword()} />
            </div>
        </div>
    );
}
