import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import Structure from '../../Structure';
import { colors } from '@components/typo';
import './index.css';
import ScamSearch from './ScamSearch';
import Video from './TestVideo'

const AgreementBox = styled.div`
    margin-top: 10px;
    font-size: 13px;
`;

const Links = styled(Link)`
    color: ${colors.defaultColor};
`;

export default function Switch() {
    const Description = () => {
        return (
            <div>
                <AgreementBox>
                    By proceeding, I agree to Sojiare <Links to="/terms-and-conditions">Terms of Use</Links> and
                    acknowledge that I have read the <Links to="/policy">Privacy Policy</Links>.
                </AgreementBox>
            </div>
        );
    };

    return (
        <Structure description={<Description />}>
            <ScamSearch />
        </Structure>
    );
}
